import React from 'react';
import PropTypes from 'prop-types';

import Portal from '../portal/Portal';
import Icon from '../icon/Icon';
import Button from '../button/Button';
import './AddingForm.css';
import SimpleExample, { MyCarousel, SimpleSlider } from '../Slider/Slider';
import { NavLink } from 'react-router-dom';

const AddingForm = ({
  isOpen, onCancel, onSubmit,description
}) => {

  return (
    <>
      {isOpen &&
        <Portal>
          <div className="modalOverlay">
            <div className="modalWindow">
              <div className="modalHeader">
                <div className="modalTitle">    </div>
                <Icon name="times" onClick={onCancel} />
              </div>
              <div className="modalBody">
              <div className="modalTitle">    {description.nominal} {description.nominal === 1 && ''}{description.nominal === 3 && ''}{description.nominal === 5 && ''}
                  {description.nominal === 10 && ''}{description.nominal === 25 && ''}{description.nominal === 50 && ''}{description.nominal === 100 && ''} {description.year}  {description.upravl}-{description.director}-{description.kassir}</div>
                <div>  </div>
                <div><textarea></textarea></div>
                <Button onClick={onSubmit} > </Button>

              </div>
              <div className="modalFooter">
                <Button onClick={onCancel} invert></Button>
                <Button onClick={onSubmit} ></Button>

              </div>
            </div>
          </div>
        </Portal>
      }
    </>
  );
};

AddingForm.propTypes = {
  title: PropTypes.string,
  info: PropTypes.string,
  isOpen: PropTypes.bool,
  onCancel: PropTypes.func,
  onSubmit: PropTypes.func,
  children: PropTypes.node,
};

AddingForm.defaultProps = {
  title: 'Modal title',
  
  isOpen: true,
  onCancel: () => { },
  onSubmit: () => { },
  children: null,
};

export default AddingForm;
