import React from 'react';
import PropTypes from 'prop-types';

import Portal from '../portal/Portal';
import Icon from '../icon/Icon';
import Button from '../button/Button';
import './Modal.css';
import SimpleExample, { MyCarousel, SimpleSlider } from '../Slider/Slider';
import { NavLink } from 'react-router-dom';

const Modal = ({
  isOpen, onCancel, description
}) => {

  return (
    <>
      {isOpen &&
        <Portal>
          <div className="modalOverlay">
            <div className="modalWindow">
              <div className="modalHeader">
                {/* upravl: '', director: '', kassir: '', nominal: 1 */}
                <div className="modalTitle">{description.nominal} {description.nominal === 1 && ''}{description.nominal === 3 && ''}{description.nominal === 5 && ''}
                  {description.nominal === 10 && ''}{description.nominal === 25 && ''}{description.nominal === 50 && ''}{description.nominal === 100 && ''} {description.year}  {description.upravl}-{description.director}-{description.kassir}</div>
                <Icon name="times" onClick={onCancel} />
              </div>
              <div className="modalBody">
                <SimpleSlider photos={description.photos} />
                <div>:{description.common}</div>
                <div>:{description.varieties}</div>
                <div><NavLink to='/numbers'>:</NavLink>{description.numbers}</div>
                <div> :{description.astimation}</div>
                <div> :{description.addInfo}</div>
                <div><NavLink to='/sourses'>/:</NavLink>{description.sourses}</div>
              </div>
              <div className="modalFooter">
                <Button onClick={onCancel} invert></Button>
                <Button  >   </Button>

              </div>
            </div>
          </div>
        </Portal>
      }
    </>
  );
};

Modal.propTypes = {
  title: PropTypes.string,
  info: PropTypes.string,
  isOpen: PropTypes.bool,
  onCancel: PropTypes.func,
  onSubmit: PropTypes.func,
  children: PropTypes.node,
};

Modal.defaultProps = {
  title: 'Modal title',

  isOpen: true,
  onCancel: () => { },
  onSubmit: () => { },
  children: null,
};

export default Modal;
